import { useFeedbackOneStaffAnalytic } from "../../../../Hooks/ApiHooks/InstituteFeedback/institute-feedback-api";
import Loading from "../../../../Loader/Loading";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useTranslation } from "react-i18next";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import styleIF from "../../InstituteFeedback.module.css";
const OneStaffAnalytic = ({ onClose, ifid, sid, staff }) => {
  const { t } = useTranslation();

  const { feedbackOneStaffAnalytic, feedbackOneStaffAnalyticLoading } =
    useFeedbackOneStaffAnalytic({
      data: {
        ifid: ifid,
        sid: sid,
      },
      skip: !sid ? !sid : !ifid,
    });

  //   console.info("feedbackOneStaffAnalytic", feedbackOneStaffAnalytic);
  return (
    <Modal onClose={onClose}>
      <div
        className={style.modal_container}
        style={{
          maxWidth: "60rem",
        }}
      >
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>
                {t("staff_analytic")} -{" "}
                {`${staff?.staffFirstName ?? ""} ${
                  staff?.staffMiddleName ?? ""
                } ${staff?.staffLastName ?? ""}`}{" "}
              </h6>
            </div>
            <img
              src={`${assestsUserFinanceUrl}/close.svg`}
              alt="close icon"
              onClick={onClose}
            />
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper
          customStyle={{
            paddingBottom: "2rem",
          }}
        >
          <section
            className={styleIF.osa_card}
            style={{
              borderTop: "0.5px solid #cacaca",
            }}
          >
            <h6
              style={{
                "--sc-width": "4rem",
              }}
            >
              {t("sr_no")}
            </h6>
            <h6
              style={{
                "--sc-width": "13rem",
              }}
            >
              {t("subject_name")}
            </h6>
            <h6
              style={{
                "--sc-width": "6rem",
              }}
            >
              {t("excellent_in_pre")}
            </h6>
            <h6
              style={{
                "--sc-width": "6rem",
              }}
            >
              {t("good_in_pre")}
            </h6>
            <h6
              style={{
                "--sc-width": "6rem",
              }}
            >
              {t("satisfactory_in_pre")}
            </h6>
            <h6
              style={{
                "--sc-width": "6rem",
              }}
            >
              {t("average_in_pre")}
            </h6>
            <h6
              style={{
                "--sc-width": "6rem",
              }}
            >
              {t("poor_in_pre")}
            </h6>
            <h6
              style={{
                "--sc-width": "8rem",
              }}
            >
              {t("average_feedback_in_pre")}
            </h6>
          </section>
          {feedbackOneStaffAnalytic?.analytic?.map((ana, index) => (
            <section key={ana?._id} className={styleIF.osa_card}>
              <h6
                style={{
                  "--sc-width": "4rem",
                }}
              >
                {index > 9 ? index + 1 : `0${index + 1}`}
              </h6>
              <h6
                style={{
                  "--sc-width": "13rem",
                }}
              >
                {ana?.subject_master?.subjectName ?? ""}
              </h6>
              <h6
                style={{
                  "--sc-width": "6rem",
                }}
              >
                {ana?.feedback_analytic?.[0]?.excellent?.percentage ?? 0}
              </h6>
              <h6
                style={{
                  "--sc-width": "6rem",
                }}
              >
                {ana?.feedback_analytic?.[0]?.good?.percentage ?? 0}
              </h6>
              <h6
                style={{
                  "--sc-width": "6rem",
                }}
              >
                {ana?.feedback_analytic?.[0]?.satisfaction?.percentage ?? 0}
              </h6>
              <h6
                style={{
                  "--sc-width": "6rem",
                }}
              >
                {ana?.feedback_analytic?.[0]?.average?.percentage ?? 0}
              </h6>
              <h6
                style={{
                  "--sc-width": "6rem",
                }}
              >
                {ana?.feedback_analytic?.[0]?.poor?.percentage ?? 0}
              </h6>
              <h6
                style={{
                  "--sc-width": "8rem",
                }}
              >
                {ana?.feedback_analytic?.[0]?.avg_percentage ?? 0}
              </h6>
            </section>
          ))}

          {feedbackOneStaffAnalyticLoading ? (
            <Loading />
          ) : (
            feedbackOneStaffAnalytic?.analytic?.length === 0 && (
              <EmptyMessage title="No staff analytic found." />
            )
          )}
        </UserMemberContentWrapper>
      </div>
    </Modal>
  );
};

export default OneStaffAnalytic;
